<template>
  <b-card>
    <div class="d-flex justify-content-between mb-2">
      <download-excel
        :data="exportItems"
        :fields="exportFields"
        :filename="'Employee'"
        :sheetname="'Employee'"
        class="cursor-pointer"
      >
        <b-button
          variant="outline-secondary"
          size="sm"
          class="mr-1"
          style="padding: 0.6rem"
        >
          <feather-icon icon="FileIcon" size="20" class="text-success" />
          <label style="font-size: 14px; margin-left: 5px; cursor: pointer"
            >Export</label
          >
        </b-button>
      </download-excel>
    </div>
    <div class="d-flex justify-content-between align-items-center mb-1">
      <div class="d-flex align-items-center">
        <label for="" class="mr-1" style="font-size: 14px">Sort</label>
        <b-form-select
          id="perPageSelect"
          v-model="perPage"
          size="sm"
          :options="pageOptions"
          class="w-100"
        />
      </div>
      <div class="d-flex align-items-center">
        <label for="" class="mr-1" style="font-size: 14px">Search</label>
        <b-form-input
          id="searchInput"
          v-model="filter"
          size="sm"
          type="search"
          placeholder="Search"
        />
      </div>
    </div>
    <b-row>
      <b-col cols="12">
        <b-table
          striped
          hover
          responsive
          :per-page="perPage"
          :current-page="currentPage"
          :items="items"
          :fields="fields"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
          :filter="filter"
          @filtered="onFiltered"
        >
          <template #cell(profile_photo_url)="data">
            <b-avatar :src="data.value" />
          </template>

          <template #cell(actions)="data">
            <div class="text-center">
              <feather-icon
                icon="TrashIcon"
                @click="deleteEmployee(data.value)"
                size="20"
                class="text-danger"
                style="margin-right: 5px"
                v-if="hasPermission('delete user')"
              ></feather-icon>
              <b-link
                :to="{
                  name: 'hrm/employee/detail',
                  params: { id: data.value },
                }"
              >
                <feather-icon
                  icon="EditIcon"
                  size="20"
                  class="text-primary"
                ></feather-icon>
              </b-link>
            </div>
          </template>
        </b-table>
      </b-col>

      <b-col cols="12">
        <div class="d-flex justify-content-between align-items-center">
          <span>
            Showing
            {{ perPage > totalRows ? totalRows : perPage }} of
            {{ totalRows }} entries
          </span>
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            align="right"
            size="sm"
            class="my-0"
          />
        </div>
      </b-col>
    </b-row>
  </b-card>
</template>

<script>
import ToastificationContent from "@/@core/components/toastification/ToastificationContent.vue";

export default {
  data() {
    return {
      perPage: 5,
      pageOptions: [3, 5, 10],
      totalRows: 1,
      currentPage: 1,
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      fields: [
        {
          key: "profile_photo_url",
          label: "Avatar",
        },
        {
          key: "user_id",
          label: "Employee",
        },
        { key: "strategic_goals", label: "Goals" },
        { key: "key_performance_indicator", label: "Description" },
        { key: "weight", label: "Weight" },
        { key: "target", label: "Target" },
        { key: "due_date", label: "Due Date", sortable: "true" },
        { key: "actions", label: "Actions" },
      ],
      items: [],

      // export excel
      exportFields: {
        "HR Code": "hrcode",
        Name: "name",
        Email: "email",
        Department: "department.name",
        Position: "team.name",
        Status: "is_active",
      },
      exportItems: [],
    };
  },
  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => ({ text: f.label, value: f.key }));
    },
  },
  mounted() {
    // Set the initial number of items
    this.totalRows = this.items.length;

    // fetch data
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.callApi({
        method: "GET",
        url: "/goals/fetch",
        success: (response) => {
          response.result.data.forEach((item) => {
            this.items.push({
              user_id: item.user_id,
              strategic_goals: item.strategic_goals,
              key_performance_indicator: item.key_performance_indicator,
              weight: item.weight,
              target: item.target,
              due_date: item.due_date,
              actions: item.id,
            });
          });
          //
          this.totalRows = this.items.length;

          // export excel
          this.exportItems = response.result.data;
        },
        error: (error) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Failed to fetch data",
              variant: "error",
            },
          });
        },
      });
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    deleteEmployee(id) {
      this.callApi({
        method: "DELETE",
        url: "/users/delete/" + this.hashid(id),
        success: (response) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Delete successfully",
              variant: "success",
            },
          });
          this.items = [];
          this.fetchData();
        },
        error: (error) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Failed to delete",
              variant: "error",
            },
          });
        },
      });
    },
  },
};
</script>
